export class Item
{
	private readonly answer: string;
	private readonly text?: string;
	private readonly images?: Array<string>;

	private userAnswer?: string;

	constructor(answer: string, text?: string, images?: Array<string>, userAnswer?: string)
	{
		this.answer = answer;
		this.text = text;
		this.images = images ? images : [];
		this.userAnswer = userAnswer;
	}

	public getText(): string | undefined
	{
		return this.text;
	}

	public getImages(): Array<string> | undefined
	{
		return this.images;
	}

	public getAnswer(): string
	{
		return this.answer;
	}

	public getUserAnswer(): string | undefined
	{
		return this.userAnswer;
	}

	public setUserAnswer(value?: string)
	{
		this.userAnswer = value ? value.trim() : undefined;
	}

	public isAnswerValid(): boolean{
		const userAnswer = this.getUserAnswer();
		if (userAnswer && this.answer.toLowerCase() === userAnswer.toLowerCase()){
			return true;
		}

		return false;
	}
}



