import {DataSet} from "../domain/DataSet";
import {Question} from "../domain/Question";
import {Item} from "../domain/Item";

export class DataSetFactory
{
	public static create(data: any): DataSet
	{
		const questions = this.createQuestions(data.questions);

		return new DataSet(data.title, questions);
	}

	private static createQuestions(data: any): Array<Question>
	{
		const questions = new Array<Question>();
		for (let question of data) {
			const items = this.createItems(question.items);
			questions.push(new Question(items, question.text, question.images as Array<string>));
		}

		return questions;
	}

	private static createItems(data: any): Array<Item>
	{
		const items = new Array<Item>();
		for (let item of data) {
			items.push(new Item(item.answer, item.text, item.images as Array<string>, item.userAnswer));
		}

		return items;
	}
}



