import * as React from "react";
import {Question} from "../../domain/Question";
import {Table} from "reactstrap";
import Slider from "react-slick";
import Lightbox from "react-images";

export interface Props
{
	questions: Array<Question>;
}

export interface State
{
	showImage?: string;
}

export class QuestionResult extends React.Component<Props, State>
{
	constructor(props: Readonly<Props>)
	{
		super(props);

		this.state = {}
	}

	public render()
	{
		return <div>
			<Table>
				<thead>
				<tr>
					<th>#</th>
					<th>Question</th>
					<th>Answers</th>
					{/*<th>Score</th>*/}
				</tr>
				</thead>
				<tbody>
				{this.props.questions.map((value, index) => {
					const imgs = value.getImages();
					return (
						<tr>
							<th scope="row">{index + 1}</th>
							<td>
								{value.getText()}
								{imgs &&
                                <div style={{maxWidth: "300px"}}>
                                    <Slider dots={true}>
										{imgs.map((v) => {
											return (
												<div>
													<img className={"img-small"} src={v}
													     alt={v} style={{margin: "0 auto"}}
													     onClick={() => {
														     this.setState({
															     showImage: v
														     });
													     }}
													/>
												</div>
											);
										})}
                                    </Slider>
                                </div>
								}
							</td>
							<td>
								<Table bordered size={"sm"} responsive>
									<thead>
									<th>-</th>
									<th>User Answer</th>
									<th>Correct Answer</th>
									</thead>
									{value.getItems().map((value => {
										const images = value.getImages();
										return (
											<tr className={value.isAnswerValid() ? "table-success" : "table-danger"}>
												<td>
													{value.getText()}<br/>
													<div style={{maxWidth: "300px", margin: "0 auto"}}>
														<Slider>
															{images && images.map((img) => {
																return (
																	<div>
																		<img className={"img-small"} src={img}
																		     alt={img} style={{margin: "0 auto"}}
																		     onClick={() => {
																			     this.setState({
																				     showImage: img
																			     });
																		     }}
																		/>
																	</div>
																);
															})}
														</Slider>
													</div>
												</td>
												<td>{value.getUserAnswer()}</td>
												<td>{value.getAnswer()}</td>
											</tr>
										);
									}))}
								</Table>
							</td>
						</tr>
					);
				})
				}
				</tbody>
			</Table>


			{this.state.showImage &&
            <Lightbox
                images={[{src: this.state.showImage}]}
                isOpen={!!this.state.showImage}
                showImageCount={false}
                backdropClosesModal={true}
                onClose={() => this.setState({showImage: undefined})}
            />
			}
		</div>;
	}


}



