import * as React from "react";
import {Question} from "../domain/Question";
import {ItemComponent} from "./ItemComponent";
import Slider from "react-slick";
import Lightbox from "react-images";

interface Props
{
	question: Question;
}

interface State
{
	showImage?: string;
	// srcSet: Array<string>;
}

export class QuestionComponent extends React.Component<Props, State>
{

	constructor(props: Readonly<Props>)
	{
		super(props);

		this.state = {
			// srcSet: []
		}
	}

	public render()
	{
		const q = this.props.question;

		const images = [];
		const src = q.getImages();
		for (let key in src) {
			images.push(
				<div>
					<img className={"Img"} src={src[key]} alt={src[key]} onClick={() => {
						this.setState({
							showImage: src[key],
						});
					}}/>
				</div>
			);
		}

		const settings = {
			dots: true,
			infinite: false,
			variableWidth: true,
			slidesToScroll: 1,
			slidesToShow: 1
		};

		return (
			<div className={"container"}>
				<div>
					<h3>
						{q.getText()}
					</h3>
				</div>

				<div className={"mb"}>
					{images.length > 0 &&
                    <Slider {...settings} className={"Slider"}>
						{images}
                    </Slider>
					}
				</div>

				<hr/>
				<hr/>

				<div>
					{q.getItems().map(((value) => {
						return <div>
							<ItemComponent item={value} key={JSON.stringify(value)}/>
							<hr/>
						</div>
					}))
					}
				</div>

				{this.state.showImage &&
                <Lightbox
                    images={[{src: this.state.showImage}]}
                    isOpen={!!this.state.showImage}
                    showImageCount={false}
                    backdropClosesModal={true}
                    onClose={() => this.setState({showImage: undefined})}
                />
				}
			</div>
		);
	}
}



