import * as React from "react";
import {Item} from "../domain/Item";
import {Input} from "reactstrap";
import Slider from "react-slick";
import Lightbox from "react-images";

interface Props
{
	item: Item;
}

interface State
{
	value: string;
	error: boolean;
	showImage?: string;
}

export class ItemComponent extends React.Component<Props, State>
{

	constructor(props: Readonly<Props>)
	{
		super(props);

		this.state = {
			value: "",
			error: false
		}
	}

	public render()
	{
		const item = this.props.item;
		const images = item.getImages();
		const className = this.state.value.length > 0 ? (this.state.error ? "is-invalid" : "is-valid") : undefined;

		const settings = {
			dots: true,
			infinite: false,
			centerMode: true,
			variableWidth: true,
			slidesToScroll: 1,
			slidesToShow: 1
		};

		return <div className={"container"}>
			{images && images.length > 0 &&
            <div className={"mb"}>
                <Slider {...settings} className={"Slider"}>
					{images.map(value => {
						return (
							<div>
								<img className={"Img"} src={value} alt={value}
								     onClick={() => {
									     this.setState({
										     showImage: value,
									     });
								     }
								     }
								/>
							</div>
						);
					})}
                </Slider>
            </div>
			}
			<div>
				{item.getText() &&
                <span>{item.getText()}</span>
				}
			</div>
			<div>
				<Input
					className={className} value={this.state.value}
					placeholder={"Your answer goes here"}
					onChange={(e) => {
						const value = e.target.value;
						this.props.item.setUserAnswer(value);
						this.setState({value: value, error: !this.props.item.isAnswerValid()})
					}}
					onPaste={(e) => e.preventDefault()}
				/>
			</div>

			{this.state.showImage &&
            <Lightbox
                images={[{src: this.state.showImage}]}
                isOpen={!!this.state.showImage}
                showImageCount={false}
                backdropClosesModal={true}
                onClose={() => this.setState({showImage: undefined})}
            />
			}
		</div>
	}
}



