import * as React from 'react';
import './App.css';
import {DataSet} from "./domain/DataSet";
import {DataSetFactory} from "./factory/DataSetFactory";
import {QuestionComponent} from "./components/QuestionComponent";
import {Button} from "reactstrap";
import {QuestionResult} from "./components/Result/QuestionResult";

interface Props
{

}

interface State
{
	dataSet?: DataSet;
	actualQuestion: number;
	score: number;
	showResult: boolean;
}

class App extends React.Component<Props, State>
{
	constructor(props: Readonly<Props>)
	{
		super(props);

		this.state = {
			showResult: false,
			actualQuestion: 0,
			score: 0
		}
	}

	public render()
	{
		const questions = this.state.dataSet ? this.state.dataSet.getQuestions() : null;
		const question = questions ? questions[this.state.actualQuestion] : null;
		const showResult = this.state.showResult;

		console.log(questions);

		return (
			<div className="App">
				<header className="App-header">
					{/*<img src={logo} className="App-logo" alt="logo"/>*/}
					<h1 className="App-title">MemorApp</h1>
					{this.state.dataSet &&
                    <h2>{this.state.dataSet.getTitle()}</h2>
					}
					{questions && !showResult &&
                    <div>
                        <h4>{this.state.actualQuestion + 1} / {questions.length}</h4>
                        <h5>Score: {this.state.score}</h5>
                    </div>
					}
					{questions && showResult &&
                    <div>
                        <h4>Result</h4>
                        <h5>Score: {this.state.score}</h5>
                    </div>
					}
				</header>
				{!showResult && questions && question &&
                <div className={"col-sm-12"}>
                    <QuestionComponent question={question}/>

                    <div className="container" style={{padding: '.5rem'}}>
                        <Button color={"danger"} className={"float-left"} onClick={() => {
							this.resetState();
							this.loadData(false);
	                        this.resetState();
	                        this.loadData(false);
						}
						}>Reset</Button>
                        <Button color={"primary"} onClick={() => this.onClickCheck()}
                                className={"float-right"}>{this.state.actualQuestion +1 >= questions.length ? "Result" : "Next"}</Button>
                    </div>
                </div>
				}

				{showResult && questions &&
                <div>
                    <QuestionResult questions={questions}/>
                    <div className="container" style={{padding: '.5rem'}}>
                        <Button color={"danger"} className={"float-left"} onClick={() => {
							this.resetState();
							this.loadData(false);
	                        this.resetState();
	                        this.loadData(false);
						}
						}>Reset</Button>
                    </div>
                </div>
				}
			</div>
		);
	}

	public componentWillMount(): void
	{
		// this.resetState()
		this.loadData();
	}

	private onClickCheck(): void
	{
		const dataSet = this.state.dataSet;
		if (!dataSet) {
			return;
		}

		const questions = this.state.dataSet ? this.state.dataSet.getQuestions() : null;
		const question = questions ? questions[this.state.actualQuestion] : null;

		if (!question) {
			return;
		}

		let good = 0;
		for (let item of question.getItems()) {
			if (item.isAnswerValid()) {
				good += 1;
			}
		}

		const {actualQuestion, totalScore} = this.saveState(good, undefined, true);

		this.setState({
			score: totalScore,
			actualQuestion: actualQuestion,
			showResult: actualQuestion >= dataSet.getQuestions().length
		});
	}

	// private onClickSkip(): void
	// {
	//
	// 	const dataSet = this.state.dataSet;
	// 	if (!dataSet) {
	// 		return;
	// 	}
	//
	// 	const actualQuestion = this.state.actualQuestion + 1;
	//
	// 	if (actualQuestion < dataSet.getQuestions().length) {
	// 		this.setState({
	// 			actualQuestion: actualQuestion
	// 		});
	// 	}
	//
	// 	if (actualQuestion >= dataSet.getQuestions().length) {
	// 		this.setState({
	// 			showResult: actualQuestion >= dataSet.getQuestions().length
	// 		})
	// 	}
	// }

	private saveState(good: number, dataSet?: DataSet, plusOneQuestion: boolean = false)
	{

		const stringify = JSON.stringify(dataSet ? dataSet : this.state.dataSet);
		const actualQuestion = plusOneQuestion ? this.state.actualQuestion + 1 : this.state.actualQuestion;
		const totalScore = this.state.score + good;
		localStorage.setItem('state', stringify);
		localStorage.setItem('actualQuestion', actualQuestion.toString());
		localStorage.setItem('score', totalScore.toString());

		return {actualQuestion, totalScore};
	}

	private resetState()
	{
		localStorage.removeItem('state');
		localStorage.removeItem('actualQuestion');
		localStorage.removeItem('score');
	}


	private loadData(loadLocalState: boolean = true): void
	{
		fetch('./dataset/elsa.json')
			.then(response => {
				response.json().then((data: any) => {
					const state = localStorage.getItem('state');
					const actualQuestion = localStorage.getItem('actualQuestion');
					const score = localStorage.getItem('score');

					const dataSet = DataSetFactory.create(loadLocalState && state ? JSON.parse(state) : data);

					if (!loadLocalState) {
						this.saveState(0, dataSet);
					}

					const doneActualQuestion = loadLocalState && actualQuestion ? parseInt(actualQuestion) : 0;
					this.setState({
						dataSet: dataSet,
						actualQuestion: doneActualQuestion,
						score: loadLocalState && score ? parseInt(score) : 0,
						showResult: doneActualQuestion >= dataSet.getQuestions().length
					});
				});
			})
			.catch((e) => console.error(e));
	}

}

export default App;



