import {Item} from "./Item";

export class Question
{
	private readonly items: Array<Item>;
	private readonly text?: string;
	private readonly images?: Array<string>;

	constructor(items: Array<Item>, text?: string, images?: Array<string>)
	{
		this.text = text;
		this.images = images;
		this.items = items;
	}

	public getItems(): Array<Item>
	{
		return this.items;
	}

	public getText(): string | undefined
	{
		return this.text;
	}

	public getImages(): Array<string> | undefined
	{
		return this.images;
	}
}



